import { recommendMusicAPI, hotMusicAPI, lbtAPI } from "@/api";
import SongItem from "@/components/SongItem.vue";
export default {
  data() {
    return {
      recommendList: [],
      hotList: [],
      lbtList: []
    };
  },

  methods: {
    playFn() {
      this.$router.push({
        path: "/play"
      });
    }

  },

  async created() {
    //推荐歌单
    const res = await recommendMusicAPI({
      limit: 9
    });
    this.recommendList = res.data.result; //  console.log("推荐请求：", this.recommendList);
    //推荐新音乐

    const songRes = await hotMusicAPI({
      limit: 100
    });
    this.hotList = songRes.data.result; // console.log("推荐新音乐请求：", this.hotList);
    //轮播图

    const lbtres = await lbtAPI();
    this.lbtList = lbtres.data.banners; //  console.log("轮播图请求：",this.lbtList);
  },

  components: {
    SongItem
  }
};