// 获取歌曲详情和 歌曲的歌词接口
import { playAPI } from "@/api";
import { hotSearchAPI, searchResultAPI } from "@/api";
import bus from "@/eventBus";
import { Icon } from 'vant';
import { onBeforeMount } from 'vue';
export default {
  components: {
    [Icon.name]: Icon
  },

  data() {
    return {
      mgs: this.$route.query.id,
      playState: true,
      // 音乐播放状态(true暂停, false播放)
      yinye: '',
      tp: this.$route.query.tps //音乐图片传值

    };
  },

  computed: {
    needleDeg() {
      // 留声机-唱臂的位置属性
      return this.playState ? '-7deg' : '-38deg';
    }

  },
  methods: {
    async audioStart() {
      // 播放按钮 - 点击事件
      if (!this.playState) {
        // 如果状态为false
        this.$refs.audio.play(); // 调用audio标签的内置方法play可以继续播放声音
      } else {
        this.$refs.audio.pause(); // 暂停audio的播放
      }

      this.playState = !this.playState; // 点击设置对立状态

      const res = await playAPI({
        id: this.mgs
      });
    }

  },

  async activated() {
    const res = await playAPI({
      id: this.mgs
    });
    this.yinye = res.data.data[0].url;
    this.mgs = this.$route.query.id;
    this.tp = this.$route.query.tps;
  },

  deactivated() {
    location.reload();
  }

};